import React, { useState } from "react"
import { handleResetPassword } from "../../database/functions"

export default function ResetPassword() {
  const [email, setEmail] = useState("")
  const [message, setMessage] = useState("")

  const onSuccess = () => setMessage("✅ Password reset link sent! Check your email.")
  const onFail = (err?: string) => setMessage("❌ " + (err || "Failed to send reset email."))

  return (
    <div style={{ maxWidth: 400, margin: "auto", padding: 20, display: "flex", flexDirection: "column", gap: 10 }}>
      <h2 style={{ textAlign: "center" }}>Reset Password</h2>

      <form
        onSubmit={(e) => {
          e.preventDefault()
          handleResetPassword({ email, onSuccess, onFail })
        }}
        style={{ display: "flex", flexDirection: "column", gap: 10 }}
      >
        <input
          type="email"
          placeholder="Email"
          value={email}
          onChange={(e) => setEmail(e.target.value)}
          required
          style={{ padding: 10, border: "1px solid #ccc", borderRadius: 5 }}
        />

        <button
          type="submit"
          style={{
            padding: 10,
            borderRadius: 5,
            background: "#F57C00",
            color: "#fff",
            border: "none",
            cursor: "pointer",
          }}
        >
          Send Reset Link
        </button>
      </form>

      {message && <p style={{ textAlign: "center", marginTop: 10 }}>{message}</p>}
    </div>
  )
}
